import React from "react";
import { getAuth, signInWithEmailAndPassword } from "firebase/auth";
import {Link, NavLink, Route, useHistory} from "react-router-dom";
import {FormAuth} from "../FormsAuth/FormAuth";

const Welcome = (props) => {
    const {push} = useHistory()
    const handleLogin = (email, password) => {
        const auth = getAuth();
        signInWithEmailAndPassword(auth, email, password)
            .then(({user}) => {
                props.setUser(user.email, user.accessToken, user.uid)
                push('/')
            })
            .catch((error) => {
                let msgError = '';
                if (error.code === 'auth/too-many-requests') {
                    msgError = '–°–ª–∏—à–∫–æ–º –º–Ω–æ–≥–æ –∑–∞–ø—Ä–æ—Å–æ–≤. –ü–æ–ø—Ä–æ–±—É–π—Ç–µ –ø–æ–∑–∂–µ.'
                }
                else if (error.code === 'auth/wrong-password' || error.code === 'auth/user-not-found') {
                    msgError = '–ù–µ–≤–µ—Ä–Ω—ã–π –ª–æ–≥–∏–Ω –∏–ª–∏ –ø–∞—Ä–æ–ª—å.'
                }
                else if (error.code === 'auth/invalid-email') {
                    msgError = '–í–≤–µ–¥–∏—Ç–µ –¥–∞–Ω–Ω—ã–µ.'
                }
                else {
                    msgError = '–ù–µ–∏–∑–≤–µ—Å—Ç–Ω–∞—è –æ—à–∏–±–∫–∞: ' + error.code
                }
                props.setMessageError(msgError)
            }
        )
    }
    return <FormAuth title={"–í–æ–π—Ç–∏"} handleClick={handleLogin} messageError={props.messageError} setMessageError={props.setMessageError} />

}

export default Welcome;